import Cookie from 'tiny-cookie';
import _ from 'lodash';
// import {cloneDeep, reduce, each, sample} from 'lodash/fp';

var COOKIE_NAME = "pf_favorites";


export const capitalize = (string) => string.charAt(0).toUpperCase() + string.slice(1);


export const dublicate = (obj, num, state, getters) => {
	let result = [];

	let seed = _.cloneDeep(getters.parents[0]);
	let filters = _.reduce(getters.filters(), function(acc, s){

		acc.push(_.cloneDeep(s.options));
		return acc;
	}, []);

	for (var i = 0; i < num; i++) {
		let el = _.cloneDeep(seed);
		el.filters = [];

		_.each(filters, (element) => {

			el.filters.push(_.sample(element));
		});

		el.id = i*2+Math.random();

		el.parent1.name += i;
		el.parent2.name += i;
		el.location += i;

		result.push(el);
	}

	return result;
}

export const getFavorites = () => {
	var cookie = Cookie.get(COOKIE_NAME);
	return (cookie === null) ? [] : JSON.parse(cookie);
}

export const setFavorites = (arr) => {
	var now = new Date;
	now.setMonth(now.getMonth() + 1);

	Cookie.set(COOKIE_NAME, JSON.stringify(arr), { expires: now.toGMTString() });
}

