export const merge = (arr1, arr2, range, idSelector) => {
	let arr2Index = 0;
	let res = [];



	for (var i = 0; i < arr1.length; i++) {
		var nameTag = '';

		// if(arr1[i].parent1 && arr1[i].parent1.first_name) {
		// 	nameTag+=arr1[i].parent1.first_name;
		// }

		// if(arr1[i].parent2 && arr1[i].parent2.first_name) {
		// 	nameTag+=arr1[i].parent2.first_name;
		// }

		// Object.assign(arr1[i], {nameTag: nameTag});

		if(i>0 && i%range == 0) {

			let arr2El = _.cloneDeep(arr2[arr2Index%arr2.length]);


			if(arr1[i].id) {
				arr2El.id = arr1[i].id-0.5;
			}

			if(arr1[i].account_id) {

				arr2El.account_id = arr1[i].account_id-0.5;
			}

			if (arr1[i].tag) {
				let nameTag = arr1[i].tag.replace(/.$/, String.fromCharCode(arr1[i].tag.charCodeAt(0) + 1));
				Object.assign(arr2El, {tag: nameTag});

			}



			res.push(arr2El);
			arr2Index++;

		}
		res.push(arr1[i]);
	}

	// TODO: Refactore
	// while(cur<(arr1.length + arr2.length)) {

	// }



	return res;
}

/**
 * Captures a image frame from the provided video element.
 *
 * @param {Video} video HTML5 video element from where the image frame will be captured.
 * @param {Number} scaleFactor Factor to scale the canvas element that will be return. This is an optional parameter.
 *
 * @return {Canvas}
 */
export const capture = function(video, opt) {
  var scaleFactor = 1;

  var w = video.videoWidth * scaleFactor;
  var h = video.videoHeight * scaleFactor;
  var canvas = document.createElement('canvas');
    canvas.width  = w;
    canvas.height = h;
  var ctx = canvas.getContext('2d');

  ctx.drawImage(video, 0, 0, w, h);

   return canvas;
}

export const urlExists = function (url) {
	var http = new XMLHttpRequest();
	http.open('HEAD', url, false);
	http.send();
	return http.status!=404;
}



