import _ from 'lodash';
// import {find, reduce, map} from 'lodash/fp';

import {dublicate, merge} from './utils';



export const errorMessage = state => state.errorMessage

export const loadingError = state => state.loadingError

export const searchQuery = state => state.searchQuery

export const initialized = state => state.initialized

export const loading = state => state.loading

export const options = state => state.options

export const captions = state => state.captions

export const order = state => state.order

export const adList = state => state.ads || []

export const listByState = state => state.listByState || {};


export const favorites = state => (id) => {
	let favorites = state.favorites;

	if (id !== undefined) {
		return favorites.indexOf(id) !== -1;
	}

	return favorites;
}


export const parents = state => key => {
	if(key && key.length) {
		if(state.parents[key] === undefined) {
			console.error("Display Order is uknown");
		}

		return state.parents[key];
	} 
	return state.parents.id;
};

export const getProfile = (state, getters) => (id) => {

	return _.find(getters.parents('id'), (el) => {
	
		return el.account_id === id
	});
	
}


export const filters = (state, getters) => (from, to) => {
	let filters = state.filters;

	if(from === undefined) return filters;
	return filters.slice(from, to);	
}

export const getFilter = (state, getters) => (id) => {
	// console.log(_.map(state.filters, (f)=> f.id));
	return _.find(state.filters, f => f.id === id);	
}

export const activeFilters = (state,getters) => {
	// console.log(_.reduce(getters.filters(), function(acc, s){
		
	// 	if (s.active && s.active.length){
	// 		acc = acc.concat(s.active.map(o => o.value));
	// 	}
	// 	return acc;
	// }, []))
	
	return _.reduce(getters.filters(), function(acc, s){

		if (s.active && s.active.length){

			acc[s.id] = _.map(s.active, 'value');
		}
	
		return acc;
	}, {});
}





