<template>
  <div class="pf-more-wrap">
    <a class="pf-more" v-show="allLoaded" v-on:click.prevent="fillMethod">{{caption}}</a>
  </div>
  
 
</template>

<script>

export default {

  props: {
    itemsPerPage: {
      type: Number,
      default: 10
    },

    itemsPerPage: {
      type: Number,
      default: 10
    },

    fillMethod: {
      type: Function
    },

    caption: {
      type: String,
      default: ''
    },

    allLoaded: {
      type: Boolean,
      default: false
    }
  },
  data: function () {
    return { 
      startIndex: 0
    }
  }
}
</script>

<style lang="scss">
</style>



