import _ from 'lodash';
// import {each, filter, forEach, orderBy, union, without} from 'lodash/fp';
import {setFavorites} from './utils';

export const setListByState = (state, listByState) => {
	state.listByState = listByState;
}

export const setFilter = (state, filterData) => {
	let filters = _.filter(state.filters, function(f) {return f.id === filterData.id})
	
	_.each(filters, function(f){
		f.active = filterData.value;
	});
}

export const init = (state, initStatus) => {
	state.initialized = initStatus;
}

export const setSearchQuery = (state, str) => {
	state.searchQuery = str;
}

export const deleteFilter = (state, filterData) => {
	let filters = _.filter(state.filters, function(f) {return f.id === filterData.id})
	
	_.each(filters, function(f){
		f.active = _.filter(f.active, function(o) {
			return o.value !==filterData.option.value;
		});

	});
}

export const setOrder = (state, order) => {
	state.order = order;
}


export const setLoading = (state, loading) => {

	state.loading = loading;

}

export const incrementVisibleCount = (state, n) => {
	state.options.visibleCount += n;
}


export const SetInitialState = (state, newState, favorites) => {

	try{

	
	
		_.forEach(newState.filters, f => {
			f.active = [];
			f.suffixes={};

		});

		_.forEach(newState.options.display_order, order => {
			var orderedList = [],
				list = newState.parents.id;


			switch(order) {
	          case "oldest" : {
	           
	           orderedList = _.orderBy(list, ['account_id'], 'asc');
	            
	            break;
	          }
	          case "youngest" : {
	            orderedList = _.orderBy(list, ['account_id'], 'desc');
	           
	            break;
	          }

	          case "random" : {
	            orderedList = _.shuffle(list);
	           
	            break;
	          }

	          case "first_name" : {
	            orderedList = _.orderBy(list, ['tag'], 'asc');
	           
	            break;
	          }

	        }

	        if(orderedList.length) {
	        	Object.assign(newState.parents, {[order]: orderedList});
	  
	        }
	        
	        // state.loading = false;
		});

	} catch(e) {
		state.loadingError = true;

		console.error(e);
	}



	Object.assign(state, newState);

}

export const SetFavorites = (state, favorites) => {
	Object.assign(state.favorites, favorites);
}

export const updateFavorites = (state, item) => {

	try {

		if(item.toAdd) {
			
			state.favorites = _.union(state.favorites, [item.username]);
		} else {
	
			state.favorites = _.without(state.favorites, item.username);
		}

		setFavorites(state.favorites);
	} catch(e) {
		console.error(e);
	}
	

}


