<template>

<!--   <select name="" id="" v-model="val" :data-id="filter.id" :key="filter.id" @change="applyFilter('filterByCategory')">
    <option v-if="placeholder" selected value="">{{placeholder}}</option>
    <option v-if="Array.isArray(options)"  v-for="option in options" v-bind:value="option">{{option}}</option>
    <option v-else v-for="(value,label) in options" v-bind:value="value">{{label}}</option>
  </select> -->
  <div class="pf-multiselect" >
    <multiselect
      ref='select'
      v-model="val"
      :options="opts"
      :placeholder="placeholder"
      :multiple="true"
      label="title"
      :searchable="false"
      track-by="value">

      <div class="pf-carret multiselect__select" @mousedown.prevent.stop="toggle()" slot="caret"><awicon name="angle-down"></awicon></div>
    </multiselect>
  </div>


</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Multiselect from 'vue-multiselect'
import _ from 'lodash';
// import {forEach} from 'lodash/fp';
import 'vue-awesome/icons/angle-down';
import awicon from 'vue-awesome/components/Icon.vue';
export default {
  props: ['filter'],
  methods: {
    toggle () {
      let el = this.$refs.select;
      el.isOpen
        ? el.deactivate()
        : el.activate()
    }
  },
  computed: {
    placeholder: function(){
      return this.captions[this.filter.id];
    },
  	val: {
  		get() {
        // console.log(this.getFilter(this.filter.id).active);
  			return this.getFilter(this.filter.id).active;
  		},

  		set(val) {
        this.$store.commit('setFilter', {
          id: this.filter.id,
          value: val
        });
        this.$emit('filt', 'filterByCategory');

  		}
  	},
    opts: function() {

      var opts = this.filter.options;
      var _this = this;

      var res = [];

      if(Array.isArray(opts)) {

        return opts.map(opt => {
          return {label:opt, title:opt+_getSuffix(opt), value: opt};
        });
      }

      // if the filter is location
      _.forEach(opts, function(stateArr, country) {

        // push coutnry
        res.push({label:country, title:country+_getSuffix(country), value: country});

        // push state as country/state
        _.forEach(stateArr, function(state) {

          res.push({label:state, title:state+_getSuffix(`${country}/${state}`), value: country+'\/'+state});
        });

      });

      function _getSuffix(label) {

        if(_this.filter.id !== 'location') return '';

        const counter = (_this.listByState[label]!==undefined)? _this.listByState[label].length:0;

        return ` (${counter})`;
      }



      return res;
    },
    ...mapGetters(['captions', 'getFilter','listByState'])
  },
  components: {Multiselect, awicon}
}
</script>

<style lang="scss">


</style>



