<template>
    <div class="pf-popup-video">
      <a @click.prevent="openFancybox" :href="'#'+id">
         <span class="pf-play-icon">
            <span></span>
            <div>{{captions.profile.watch}}</div>
          </span>

          <span ref="thumb" class="pf-popup-video-thumb">

          </span>
      </a>
      <div style="display: none;">
        <div :id="id" :class="'pf-video-custom'">
          <video @ended="onError"  @canplay="createThumb"  controls="" preload="metadata" name="media">
            <source :src="url"  type="video/mp4" >
          </video>
        </div>

      </div>
    </div>

</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import $ from 'jquery';
import {capture, urlExists} from '../../store/utils'

export default {
  props: ['id', 'url'],
  data: function(){
    return {

    }
  },
  computed: {
   ...mapGetters(['captions'])
  },
  created: function() {

  },
  watch: {

  },

  components: {},
  methods: {
    openFancybox: (e) => {
      var $el = $(e.currentTarget);
      var opt = Object.assign({},window.Vue.FANCYBOX);

      // if($el.data('type')) {
      //   opt.type = $el.data('type');
      // }

      $.fancybox.open( $el, opt);
    },
    onError: function() {

    },
    createThumb: function() {
      var _this = this;

      _this.$nextTick(function () {


        var canvas =  capture($(`#${_this.id} video`)[0]);


        if(canvas) {
          _this.$refs.thumb.innerHTML='';
          _this.$refs.thumb.appendChild(canvas);
          // console.log(_this.$refs.thumb.minHeight)
          _this.$refs.thumb.style.minHeight = 'auto';
        } else {
          console.error("Can't generate a thumbnail for "+_this.url);
        }
      });
    }


  }
}
</script>

<style lang="scss">


</style>



