<template>

  <div v-bind:class="[{ 'pf-disabled': !itemdata.is_enable }, 'pf-item' ]" :data-id="itemdata.id" :data-account_id = "itemdata.account_id" :data-filters="itemdata.filters">
    <!--  -->
    <div class="pf-item-inner">
      <div class="pf-img-wrap">

        <router-link v-if="itemdata.is_enable" :to="{ name: 'profile', params: { username: itemdata.username }}">
          <ItemImg :itemdata="itemdata" />
        </router-link>
        <template v-else>
          <ItemImg :itemdata="itemdata" />
        </template>

      </div>
      <div class="pf-item-title">
        <div class="pf-item-title-inner">
          <h5>{{itemdata.parent1.first_name}} <template v-if="itemdata.parent2.first_name.length">&</template> {{itemdata.parent2.first_name}}</h5>
          <h6>{{locationString}}</h6>

        </div>
        <div class="pf-item-btns">

          <a href="#" v-bind:class="[{ 'pf-active': isFavorite }, 'pf-item-btn','pf-btn-favorite' ]" @click.prevent="toggleFavorite">
            <div class="pf-icon"><Icon name="Like" :scale="100"></Icon></div>

          </a>

          <router-link v-if="itemdata.is_enable" class="pf-item-btn pf-btn-contact" :to="{ name: 'profile', params: { username: itemdata.username, popup:'#pf-call-form' }}">
            <div class="pf-icon">
              <awicon name="envelope"></awicon>
              <div>{{captions.contact}}</div>
            </div>
          </router-link>

          <span v-else class="pf-item-btn pf-btn-contact">
            <div class="pf-icon">
              <awicon name="envelope"></awicon>
              <div>{{captions.contact}}</div>
            </div>
          </span>



        </div>



      </div>
    </div>


  </div>


</template>

<script>
import ItemImg from './Img.vue'
import Icon from 'vue-svg-icon/Icon.vue';
import 'vue-awesome/icons/envelope';
import 'vue-awesome/icons/user';
import awicon from 'vue-awesome/components/Icon.vue';
import { mapGetters } from 'vuex'
export default {
  props: ['itemdata'],
  data: function() {
    return {
      isFavorite: false
    }
  },
  computed: {
    // isFavorite: function() {
    //   return this.favorites(this.itemdata.id);
    // },
    locationString: function() {
      let loc = this.itemdata.location;

      if (typeof loc === 'object') {
        if(loc.state!==null) {
          if(loc.country.toLowerCase()=="united states") {
            return loc.state;
          }
          return loc.state+' / '+loc.country;
        }

        return loc.country;
      }

      return loc;
    },
    ...mapGetters(['captions', 'favorites'])
  },
  methods: {
    toggleFavorite() {
      this.isFavorite = !this.isFavorite;

      this.$store.commit('updateFavorites', {
        toAdd: this.isFavorite,
        username: this.itemdata.username
      })
    }
  },
  components: {Icon, awicon, ItemImg},
  mounted() {
    this.isFavorite = this.favorites(this.itemdata.username);
  }
}
</script>

<style lang="scss">

</style>



