import Vue from 'vue'
import Vuex from 'vuex'
import app from './components/App.vue'
import store from './store'
import './assets/css/styles.scss'
// import './assets/css/theme.scss'
import router from './router'
import axios from 'axios'
import scrollto from 'vue-scrollto'
import $ from 'jquery'

let fancyboxOpt = {
  hash: false,
  touch: true,
  autoFocus: false,
  backFocus: false,
  baseClass: 'pf-fancybox'
}

// const paths = {
// 	'gallery': '/src/store/initStateTest.js',
// 	'profile': '/src/store/Profile.js',
//   'form': ''
// };
//
const paths = {
	'gallery': '',
	'profile': '',
  'form': ''
}



Vue.use(scrollto);
Vue.use(Vuex);

new Vue({
  el: '.pf_b-app-wrapper',
  store,
  router,
  created: function () {
    this.HTTP_PATHS = paths;
  	this.FANCYBOX = fancyboxOpt;



    window.Vue = this;

  },
  components: {app}
});



