<template>
  <div>
    <img class="pf-item-img" :src="itemdata.img" alt="">
    <div class="pf-item-label" v-if="itemdata.label && !isFamilyWaiting" v-bind:style="{ backgroundColor: itemdata.label.color}">
      <Icon name="star" />
      {{itemdata.label.title}}
    </div>
    <div class="pf-item-overlay">
      <div v-if="itemdata.is_enable">
        {{captions.seeProfile}}
        <br>
        <awicon name="user" />
      </div>

      <div v-else>
        {{captions.disableProfile}}
      </div>
    </div>
  </div>
</template>

<script>;
import Icon from 'vue-svg-icon/Icon.vue'
import 'vue-awesome/icons/user';
import awicon from 'vue-awesome/components/Icon.vue';
import { mapGetters } from 'vuex'
export default {
  props: ['itemdata'],
  data: function() {
    return {
      isFamilyWaiting: false
    }
  },
  computed: {
    ...mapGetters(['captions'])
  },
  components: {Icon, awicon},
  created: function() {
    this.isFamilyWaiting = this.itemdata.label.title == 'Waiting';
  }
}
</script>

<style lang="scss">

</style>



