<template>
    <div :class="['pf-form', 'pf-style-'+view, 'pf-cf']">
      <div class="pf-form-title">
        <div>
          <div class="pf-round-btn">
            <slot name="title-icon"></slot>
          </div>

          <slot name="title"></slot>
        </div>
        <div class="pf-form-subtitle"><slot name="subtitle"></slot> </div>
         
        
      </div>

      <form action="" v-on:submit.prevent="onSubmit">
        <div class="pf-form-error" v-if="error">{{captions.forms.error}}</div>
        <div class="pf-form-error" v-if="!valid">{{captions.forms.not_valid}}</div>
        
        <transition-group name="fade" mode="out-in">

          <div class="pf-form-success" v-if="success" key="success">{{captions.forms.success}}</div>

          <div v-if="!success" key="form">
            
            <!-- fancybox autofocus hack to prevent multielect focus on popup opening-->
            <input type="text" style="position:absolute; visibility:hidden;">


            <template  v-for = "(input, index) in form" >
              
              <div v-if="input.type=='checkbox'" class="pf-input" :key="input.name+index" >
                <div class="pf-checkbox">
                  <input type="checkbox" :data-disableHandler="input.disableHandler" :name="input.name" @change="changeValue">
                  <span><Icon name="check"></Icon></span>
                </div>
                 <span>{{input.placeholder}}</span>

              </div>
              

             <multiselect  v-else-if="input.type=='select'" :key="input.name+index"  
                :id = "input.name"
                :options="input.options"
                :placeholder="input.placeholder"
                :multiple="false"
                :searchable="false"
                :class="['pf-input']"
                :disabled = "disable"
                @input="changeValue">
                <div class="pf-carret" slot="carret"><Icon name="arrowdown"></Icon></div>
              </multiselect>

              <textarea :disabled = "disable" v-else-if="input.type=='textarea'" 
                  class="pf-input" 
                  :name="input.name"  
                  :key="input.name+index" 
                  :placeholder="input.placeholder"
                  @change="changeValue"></textarea>

              <input v-else class="pf-input" 
                      :disabled = "disable"
                      :type="input.type" 
                      :name="input.name"  
                      :key="input.name+index" 
                      :placeholder="input.placeholder"
                      @change="changeValue">
            </template>
          </div>
          
          <div class="pf-form-submit-wrap" key="submit">
            <moon-loader :loading="loading" :color="'#fff'" :size="'20px'"></moon-loader>
            <input v-if="form && form.length && !success" 
                  class="pf-submit" 
                  type="submit" 
                  value="submit"
                  @change="changeValue"
                  :disabled = "disable"
                  >
            
          </div>
          

        </transition-group>

      </form>
    </div>
    
  
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Multiselect from 'vue-multiselect'
import Icon from 'vue-svg-icon/Icon.vue';
import axios from 'axios';
import $ from 'jquery';
import 'vue-awesome/icons/phone';
import 'vue-awesome/icons/envelope';
import 'vue-awesome/icons/comments';
import 'vue-awesome/icons/angle-down';
import MoonLoader from 'vue-spinner/src/MoonLoader.vue'

import awicon from 'vue-awesome/components/Icon.vue';

export default {
  props: ['data', 'form', 'view'] ,
  data: function(){
    return {
      valid: true,
      error: false,
      success: false,
      disable: false,
      loading: false,
      formData: {}
    }
  },
  computed: {
   ...mapGetters(['captions'])
  },
  created: function() {
    this.form.forEach(input=>{
      if(input.disableHandler) {
        this.disable = true;
      }
      this.$set(this.formData, input.name, "");
    })

    
  },
  watch: {
   
  },

  components: {awicon,Multiselect, Icon, MoonLoader},
  methods: {
    onSubmit: function (event) {
      var _this = this;
      var data = {
        request: 'form',
        ...this.data,
        ...this.formData
      };
      this.loading = true;
      this.error = false;
      this.valid = true;
      this.success = false;

      $.ajax({
        url: window.Vue.HTTP_PATHS.baseUrl,
        data: data
      }).done(function(response) {
        var json = JSON.parse(response);
        _this.loading = false;
        

        if(json.status=='error') {
          _this.error = true;
        }

        if(json.status=='not_valid') {
          _this.valid = false;
        }

        if(json.status=='success') {
          _this.success = true;
        }
       
      });

    //   axios.get(window.Vue.HTTP_PATHS.form, {
    //     request: 'form',
    //     ...this.data,
    //     ...this.formData
    //   })
    // .then(function (response) {
    //     console.log(response);
    //   })
    // .catch(function (error) {
    //     console.log(error);

    //   });
    },
    changeValue: function(e,id){
      if(id!=undefined) {
        this.$set(this.formData, id, e);
      } else {

        if(e.target.dataset.disablehandler) {
          this.disable = !e.target.checked;
        }

      
       
        this.$set(this.formData, e.target.name, e.target.type=='checkbox'?e.target.checked:e.target.value);
      }
         
    },
    onError: function() {
     
    }

  }
}
</script>

<style lang="scss">
 
  
</style>



