<template>
    <div class="pf-tab">
      <div v-bind:class="[{ 'pf-open': open }, 'pf-tab-title']" @click="open = !open">
        <slot name="icon"></slot>
        <span>{{title}}</span>
      </div>
      <transition name="grow"
          v-on:before-enter="beforeEnter"
          v-on:after-enter="afterEnter"
          v-on:before-leave="beforeLeave"
          v-on:after-leave="afterLeave"
      >
        <div class="pf-tab-content animated"   v-show="open">
          <div class="pf-tab-content-inner pf-cf">
            <slot></slot>
          </div>
        </div>

      </transition>
      
    </div>
  
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  props: ['title', 'onOpen'],
  data: function(){
    return {
      open: true
    }
  },
  computed: {
   
  },
  created: function() {
    var _this = this;
    setTimeout(function() {
      _this.open = false;
    }, 100);
    
  },
  watch: {
    open: function() {
      // console.log('fromTab', this, this.$parent.open);
    }
  },

  components: {},
  methods: {
    init: function() {
      // console.log(this)
    },
    beforeEnter (el) {
        // Setup clone
        el.parentNode.classList.add("inited");
        var clone = el.cloneNode(true), h
        clone.style.width = el.style.width
        clone.style.visibility = 'hidden'
        clone.style.removeProperty('display')
        
        // if inner animation (might be undesired)
        var desc = clone.querySelectorAll("*");
        [].forEach.call(desc, function (item) {
            // remove possible inline styles of display(=none)
            item.style.removeProperty('display')
        });
        
        // get clone height
        el.parentNode.appendChild(clone)
        h = clone.clientHeight
        clone.remove()
        
        // Force animation instead of simple "setting a height"
        el.style.height = "0px"
        setTimeout(() => el.style.height = h + "px", 1)
    },
    afterEnter (el) {
        el.style.removeProperty('height');

        if(typeof this.onOpen == 'function') {
          this.onOpen();
        }
    },
    beforeLeave (el) {
      el.style.height = el.clientHeight + "px"
          setTimeout(() => el.style.height = "0px", 1)
    },
    afterLeave (el) {
          el.style.removeProperty('height')
    }

  }
}
</script>

<style lang="scss">
 
  
</style>



