<template>
    <div class="pf-journals-list pf-cf">

      <isotope ref="jlist"  :options='getOptions()' @layout.once="init" v-images-loaded:on.progress="layout" :list="list">
          <div class="pf-j-item" v-for="item in list" :key="item.journalId">
            <div class="pf-j-item-inner">
              <h5><div class="pf-j-date">{{item.journdate}}</div> {{item.Caption}} </h5>

              <div class="pf-j-content" v-html="item.Text"></div>
            </div>
            
          </div>

      </isotope>
      
      
      
    </div>
  
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import isotope from './../../isotope.js'
import imagesLoaded from 'vue-images-loaded'
import fluidvids from './../../libraries/fluidvids.js'


export default {
  props: ['list'],
  data: function(){
    return {
     
    }
  },
  computed: {
    isOpen: function() {
      return this.$parent.open;
    }
  },
  watch: {
    isOpen: function() {
      if(this.isOpen) {
        this.$refs.jlist.layout('masonry');
      }
    }
  },

  components: {isotope, imagesLoaded},
  directives: {
    imagesLoaded
  },
  methods: {
    init() {
      fluidvids.init({
        selector: ['iframe', 'object'], // runs querySelectorAll()
        players: ['www.youtube.com', 'player.vimeo.com'] // players to support
      });
    },
    layout () {
      let _this = this;
     
      this.$refs.jlist.layout('masonry');
    },
    getOptions: function () {
      var _this = this;
      return {
        layoutMode: 'masonry',
        percentPosition: true,
        itemSelector: '.pf-j-item'
      }     
    }

  },
  created: function() {
    
  }
}
</script>

<style lang="scss">
 
  
</style>



