<template>
  <div  :data-id="adv.id" v-bind:class="[{ ['pf-item-'+adv.size]: adv.size }, 'pf-item','pf-adv' ]" :key="adv.id" >
    <div  class="pf-item-inner" v-bind:style="{ backgroundColor: adv.color, backgroundImage: adv.image?'url('+adv.image+')':null}">
      <div  class="pf-adv-content pf-cf">
          <h5>{{adv.title}}</h5>
          <p v-if="adv.text">
            {{adv.text}}
          </p>
          <a v-if="adv.url && adv.link" :href="adv.url" class="pf-adv-more">{{adv.link}}</a>
      </div>

    

        
    </div>



  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  props: ['adv'],
  computed: mapGetters(['captions'])
}
</script>

<style lang="scss">
</style>



