import axios from 'axios';
import {getFavorites} from './utils';

export const setListByState = ({ commit, state }, obj) => {

  let filteredListWorld = obj.filter(obj.list, obj.filters);


  // set list filtered by location (for location select counters)
  let listByState = {};

  _.each(filteredListWorld, (item) => {
    const location = [];

    const country = item.filters.location;
    const state = item.filters.state;

    if(country && country.length) {
      location.push(country);
    }

    if(state && state.length) {
      location.push(state);
    }

    _.each(location, (loc)=> {
        if (listByState[loc]) {
          listByState[loc].push(item);
        } else {
          listByState[loc] = [item];
        }
    });
  });

  commit('setListByState', listByState);
}


export const loadInitialState = ({ commit, state }, obj) => {
  state.loading = true;
  state.errorMessage = obj.errorMessage;
	// var dataUrl = url+"&request=data";
  // console.time('load');
	var dataUrl = obj.url;
   // console.log(dataUrl);
  axios.get(dataUrl, {
    params: {
      request: "data"
    }
  })
    .then(response => {
      state.loadingError = false;

     // console.timeEnd('load');
      commit('SetInitialState', response.data);
      commit('SetFavorites', getFavorites());
      commit('init', true);

       // state.loading = false;
    })
    .then(() => {
      var dataLoad = window.Vue.$route.meta.dataLoad;

      if(typeof dataLoad === "function") {
        dataLoad();
      }
    })
    .catch(e => {
      state.loadingError = true;
      console.error(e);
    })
}



