<template>

<!--   <select name="" id="" v-model="val" :key="id" >
    <option v-if="placeholder" selected value="">{{placeholder}}</option>
    <option v-for="option in options" v-bind:value="option">{{option}}</option>
  </select> -->
  <div class="pf-multiselect">
    <multiselect
      v-model="val"
      :options="opts"
      :placeholder="placeholder"
      :searchable="false"
      label="label"
      ref='select'
      track-by="value">

      <div class="pf-carret multiselect__select" @mousedown.prevent.stop="toggle()" slot="caret"><awicon name="angle-down"></awicon></div>
    </multiselect>
  </div>


</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Multiselect from 'vue-multiselect'
import 'vue-awesome/icons/angle-down';
import awicon from 'vue-awesome/components/Icon.vue';

import _ from 'lodash';
// import {forEach} from 'lodash/fp';
export default {
  props: ['options', 'id'],
  computed: {
    placeholder: function(){

      return this.captions[this.id];
    },
  	val: {
  		get() {
  			return this.order;
  		},

  		set(val) {
  			this.$store.commit("setOrder", val);
  		}
  	},
    opts: function(){

      var opts = this.options;
      var res = [];

      if(Array.isArray(opts)) {
        return opts.map(opt => {
          return {label:this.captions[opt], value: opt};
        });
      }

      _.forEach(opts, function(value, key) {
        res.push({label:key, value: key});

        _.forEach(value, function(v) {

          res.push({label:v, value: key+'\/'+v});
        });

      });

      return res;
    },
    ...mapGetters(['captions', 'order'])
  },
  components: {Multiselect, awicon},
  methods: {
    toggle () {
      let el = this.$refs.select;
      el.isOpen
        ? el.deactivate()
        : el.activate()
    }
  }
}
</script>

<style lang="scss">


</style>



