<template>
  <div class="pf-app">

 <!--  <transition-group name="fade" appear mode="out-in">

    <div class="pf-loading pf-app-content" v-show="loading" key="loader">
      <div class="pf-loading-ico">
        <Icon name="Like" :scale="100"></Icon>
      </div>
    </div>


    <div :class="{'pf-hidden':loading}" key="content">
      <router-view></router-view>
    </div>
  </transition-group> -->

  <!--   <transition-group name="fade"  mode="out-in" >
      <div class="pf-loading pf-app-content" v-show="loading" key="loader">
        <div class="pf-loading-ico">
          <Icon name="Like" :scale="100"></Icon>
        </div>
      </div>
      
    </transition-group> -->

   <transition name="fade">
    <span class="pf-loading" v-show="!loadingError && loading" key="loader">
      <div class="pf-loading-ico">
        <Icon name="Like" :scale="100"></Icon>
      </div>
    </span>
  </transition>

  <div class="pf-error" v-if="loadingError">
    {{errorMessage}}
  </div>
    
  <router-view ></router-view>


  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import axios from 'axios';
import Icon from 'vue-svg-icon/Icon.vue';
import Gallery from './Gallery.vue'
import Profile from './Profile/Profile.vue'



export default {
  name: 'app',
  computed: {

    ...mapGetters(['parents', 'loading', 'initialized', 'loadingError', 'errorMessage'])
  },
  props: ['url', 'error'],
 
  watch: {
    
  },
  data: function(){
    return {

    }

  },
  components: {Gallery, Profile, Icon},

  methods: {

  },

  beforeMount() {
    let _this = this;
    window.Vue.HTTP_PATHS.baseUrl = (this.url && this.url.length)?this.url:window.location.href.replace(location.hash,"");
    axios.defaults.baseURL = window.Vue.HTTP_PATHS.baseUrl;

    _this.$store.dispatch('loadInitialState', {
      url: window.Vue.HTTP_PATHS.gallery,
      errorMessage: _this.error
    });

    
   
  }
}
</script>

<style lang="scss">
</style>



