<template>
<!--     <transition name="fade"  mode="out-in" v-on:after-enter="$store.commit('setLoading', false)">

    </transition> -->


    <div v-show="parents.length" key="content" v-images-loaded:on.progress="onCreated" :class="[{'pf-hidden': loading}, 'pf-app-content']">

        <div class="pf-filters pf-cf">
          <div class="pf-filters-col">
            <template  v-for="soption in options.sorting_options">
              <div  v-if="soption ==='favorite'" class="pf-filter pf-cf">

                <a  href="#" @click.prevent="toggleFavorite" v-bind:class="[{ 'pf-active': isFavoriteActive }, 'pf-fitler-btn', 'pf-btn-favorite' ]">
                  <div class="pf-icon"><Icon name="Like" :scale="100"></Icon></div>
                </a>
              </div>




               <div v-else-if="getFilter(soption)" class="pf-filter pf-cf">

                <filter-select  @filt='galleryFilter' :favorites="isFavoriteActive"  :filter="getFilter(soption)" ></filter-select>
              </div>
              <div v-else-if="soption ==='sort_by'" class="pf-filter pf-cf">
                <order-select :id="'sort_by'"  :options="options.display_order"></order-select>
              </div>


            </template>
          </div>

          <div class="pf-filter pf-search-wrap  pf-cf">
            <div class="pf-search">
              <div class="pf-search-inner">
                <input type="text" :placeholder = "captions.searchPlaceholder" v-model="search">
                <awicon name="search"></awicon>
              </div>

              <button class="pf-search-btn"  @click.prevent="activateSearch">
                <awicon name="search"></awicon>
              </button>
            </div>
          </div>
        </div>

        <div class="pf-cf">
          <transition name="slide-left">
            <div class="pf-search-field" v-show="searchActive">
              <input type="text" :placeholder = "captions.searchPlaceholder" @blur="blurSearch" ref="search" v-model="search">
              <i aria-hidden="true" tabindex="1" class="multiselect__tag-icon pf-custom"  @click = "closeSearch"></i>
            </div>
          </transition>

          <div class="pf-selected-filters pf-cf" v-if="activeFilterOptions.length">
            <span class="multiselect__tag" v-for="opt in activeFilterOptions">
              <span>{{opt.option.label}}</span> <i aria-hidden="true" tabindex="1" class="multiselect__tag-icon  pf-custom"  @click = "onFilterOptionRemove(opt)"></i>
            </span>
          </div>



        </div>



        <transition-group name="fade">

          <div class="pf-error" v-show="error" key="error">
            {{error}}
          </div>

          <div class="pf-oh" v-show="!error" key="gallery">
            <div class="pf-list">
              <isotope ref="cpt"  :options='getOptions()' v-images-loaded:on.progress="layout" :list="getFilteredList(list)">
                <template v-for="item in getFilteredList(list)">
                  <Item v-if="item.type === 'person'" :itemdata="item" :key="item.id"></Item>
                  <adv v-else-if="item.type === 'adv' " :adv="item" :key="item.id"></adv>
                </template>
              </isotope>
            </div>
          </div>
        </transition-group>


        <more :fillMethod="fillList" :all-loaded="allLoaded" :caption="captions.more"></more>
      </div>

</template>

<script>

import { mapGetters, mapActions } from 'vuex'


import {dublicate, merge, getFavorites} from '../store/utils'
import imagesLoaded from 'vue-images-loaded'
import awicon from 'vue-awesome/components/Icon.vue';
import 'vue-awesome/icons/search';
import Item from './GalleryItem/Item.vue'
import adv from './Adv.vue'
import more from './MoreBtn.vue'
import filterSelect from './filterSelect.vue'
import orderSelect from './displayOrder.vue'
import isotope from './../isotope.js'
import Icon from 'vue-svg-icon/Icon.vue';

import _ from 'lodash';
// import {filter, forEach, debounce, values, intersection} from 'lodash/fp';


export default {
  computed: {
    activeFilterOptions: function() {
       var options = [];
       _.forEach(this.filters(), f => {
        var fOptions = f.active.map(o => {
                            return {id: f.id, option: o}
                      });
        options = options.concat(fOptions);

      });

      return options;
    },
    favoriteList: function(){
      return _this.favorites();
    },

    list: function() {

      let _this = this;
      let list = (this.order!==null)?this.parents(this.order.value):this.parents();


      if(_this.isFavoriteActive) {
        let favoritesList = _this.favorites();
        list = _.filter(list, function(item) {
          return favoritesList.indexOf(item['username']) !== -1;
        });

      }




      return list;
    },

    search: {
      get() {
        return this.searchQuery;
      },

      set(val) {
        this.$store.commit('setSearchQuery', val);
      }
    },

    ...mapGetters(['filters', 'activeFilters', 'parents', 'adList', 'options', 'loading', 'captions', 'favorites', 'getFilter', 'searchQuery', 'order'])
  },

  directives: {
    imagesLoaded
  },
  watch: {
    selectActive: function() {
      if(this.selectActive) {
        this.$nextTick(function () {
           this.$refs.search.focus();
        });
      }
    },
    order: function() {
      if (this.order) {
        // var sortOpts = this.getSortDataVal(this.order.value);
        // this.$refs.cpt.iso.arrange(sortOpts);

        switch(this.order.value) {
          case "oldest" : {

            this.$refs.cpt.iso.arrange({
              sortBy: 'date',
              sortAscending: true
            });
            break;
          }
          case "youngest" : {
            this.$refs.cpt.iso.arrange({
              sortBy: 'date',
              sortAscending: false
            });

            break;
          }

          case "first_name" : {
            this.$refs.cpt.iso.arrange({
              sortBy: 'name',
              sortAscending: true
            });

            break;
          }

          case "random" : {
            this.$refs.cpt.iso.arrange({
              sortBy: null
            });

            break;
          }

          default: {
            this.$refs.cpt.iso.arrange({
              sortBy: 'id',
              sortAscending: true
            });
          }
        }

      } else {
        this.$refs.cpt.iso.arrange({
            sortBy: 'id',
            sortAscending: true
          });
      }

    }
  },
  data: function(){
    return {
      error: null,
      isFavoriteActive: false,
      allLoaded: true,
      beforeCreated: true,
      parentsList: [],
      searchActive: false
    }

  },
  components: {Item, filterSelect,  adv, more, awicon, isotope, orderSelect, Icon},
  // beforeRouteLeave (to, from, next) {
  //   this.$store.commit('setLoading', true);
  //   next();
  // },
  methods: {
    blurSearch: function(e) {
      if(!e.target.value) {
        setTimeout(()=> closeSearch, 400);
      }
    },
    activateSearch: function() {
      this.searchActive = true;
    },
    closeSearch: function() {
      this.searchActive = false;
      this.search = '';
    },
    onCreated: _.debounce(function (e) {
      this.$nextTick(function () {
        this.$store.commit('setLoading', false);
      })

    }, 500),
    getSortDataVal: function(v) {
      switch(v) {
          case "oldest" : {
            return {
              sortBy: 'date'
            }
            break;
          }
          case "youngest" : {
            return {
              sortBy: 'date',
              sortAscending: false
            }
            break;
          }

          default: {
            return {
              sortBy: 'id'
            }
          }
        }
    },
    onFilterOptionRemove: function(opt) {

      this.$store.commit('deleteFilter', opt);
    },
    toggleFavorite: function() {
      this.isFavoriteActive = !this.isFavoriteActive;
    },

    layout() {
      let _this = this;
      this.$refs.cpt.layout('masonry')
    },

    fillList: function() {
      let _this = this;
      let opt = _this.options;
      _this.$store.commit('incrementVisibleCount', opt.itemsPerPage);
    },

    filter: function(list, filters) {
      let _this = this;

     return  _.filter(list, o => {
        let query = _this.searchQuery.toLowerCase();

        let testSearchQuery = (query.length === 0) || (o.parent1.first_name.toLowerCase().search(query) != -1 || o.parent2.first_name.toLowerCase().search(query) !=-1);

        var filterRes = true;


        if(filters) {

          _.forEach(filters, (f,k) => {
            //console.log(o.account_id, userFilters, f, _.intersection(userFilters,  f))
            let selectedFilters = f.slice();


            let userFilter = o.filters[k];
            if(!Array.isArray(userFilter)) {
              userFilter = [userFilter];
            }

            if(k == 'location') {
              userFilter.push(o.filters['state']);
            }

            // console.log(f, k, userFilter, o.filters)
            filterRes = filterRes && _.intersection(userFilter,  f).length;
          });
        }

        return  filterRes && testSearchQuery;
      });

    },

    getFilteredList: function(list) {

      let _this = this;
      let opt = _this.options;

      let filteredList = _this.filter(list, _this.activeFilters);

      _this.$store.dispatch('setListByState', {
        list: list,
        filters: _.omit(this.activeFilters, 'location'),
        filter: _this.filter
      });



      let slicedList = filteredList.slice(0, opt.visibleCount);

      this.allLoaded = _this.options.visibleCount < filteredList.length;

      if(slicedList.length == 0) {
        this.error = this.captions.not_found;
      } else {
        this.error = null;
      }


      if(opt.adsRange>0) {

        return merge(slicedList, _this.adList, opt.adsRange);
      }

      return slicedList;

    },

    getOptions: function () {
      var _this = this;


      return {
        layoutMode: 'masonry',
        percentPosition: true,
        itemSelector: '.pf-item',
        masonry: {


        },

        getSortData: {
         id: "id",
         date: "account_id",
         name: "tag"
        },
        sortBy: 'id'
        // ,
        // getFilterData: {
        //   filterByCategory:  function (itemElem) {
        //     return itemElem.filters.indexOf('_all') !== -1 || _.difference(_this.activeFilters, itemElem.filters).length === 0;
        //   }


        // }


      }


    },
    galleryFilter: function(key){
      let _this = this;

      this.layout();
    },

  },
  beforeMount() {


    // const iso = new Isotope(this.$el, options)
  },

  created() {

    this.search = '';

  },


  beforeMount() {


    // const iso = new Isotope(this.$el, options)
  }
}
</script>

<style lang="scss">
</style>



