<template>
  <div>
  <!-- <transition name="fade"  mode="out-in"> </transition> -->

    <div v-if="error" key="error">
      <!-- HEADER -->
      <div class="pf-profile-header pf-cf">
        <router-link class="pf-button" :to="{ name: 'gallery'}">{{captions.profile.back_to_search}}</router-link>
      </div>
      <!-- END HEADER -->
      <div class="pf-error">
        {{error}}
      </div>
    </div>



    <div v-else-if="profile && initialized" key="content"  :class="[{'pf-hidden': loading && initialized}, 'pf-app-content', 'pf-profile']" v-images-loaded="onProfileLoaded">
      <!-- HEADER -->
        <div class="pf-profile-header pf-cf">

          <div class="pf-profile-phone" v-if="profile.agency.contact && profile.agency.contact!=='0'">
            <span>{{captions.profile.contact_us}}</span>
            <div>{{profile.agency.contact}}</div>
          </div>
          <router-link class="pf-button" :to="{ name: 'gallery'}">{{captions.profile.back_to_search}}</router-link>
        </div>
      <!-- END HEADER -->

      <!-- INFO -->
      <div class="pf-profile-info pf-cf">
        <div class="pf-profile-ava">
          <img :src="profile.info.avatar" alt="" v-if="profile.info.avatar">
        </div>

        <div class="pf-profile-desc">
          <!-- BUTTONS -->

            <div class="pf-profile-buttons">
              <div class="pf-round-btn"  :data-intro="captions.tooltips.navigation" :data-position="'bottom-right-aligned'">
                <Icon name="rectangles" :scale="100" class="pf-btn-rectangles"></Icon>

                <div class="pf-tooltip">
                  <div class="pf-tooltip-inner">

                    <div class="pf-tooltip-item" v-for="btn in tooltipButtons" :key="btn.name">
                      <template v-if="btn.icon">
                        <a :href="btn.link" @click="(btn.fancybox ? openFancybox($event) : null)" :data-type="btn.type" :target="btn.target" >
                          <div class="pf-round-btn">
                            <Icon v-if="btn.svg" :name="btn.icon" :scale="100"></Icon>
                            <awicon v-else :name="btn.icon" :scale="100" :class="['pf-btn-'+btn.icon]"></awicon>
                          </div>
                          <div>{{btn.label}}</div>
                        </a>
                      </template>

                    </div>



                  </div>
                </div>
              </div>



              <div :class="[{ 'pf-active': isFavorite }, 'pf-round-btn', 'pf-btn-like']" @click="toggleFavorite">
                <Icon name="Like" :scale="100"></Icon>
              </div>
            </div>

          <!-- END BUTTONS -->

          <!-- FORMS -->
            <div style="display:none;">

              <div id="pf-chat-form" class="pf-popup-form">
                <AjaxForm :form="forms.chat" :view="1" :data="{username:profile.info.username, form_type:'chat'}">
                  <awicon slot="title-icon" name="comments" :scale="100"></awicon>
                  <span slot="title">{{captions.forms.labels.chat}}</span>
                </AjaxForm>
              </div>

              <div id="pf-call-form" class="pf-popup-form">
                <AjaxForm :form="forms.call" :view="2" :data="{username:profile.info.username, form_type:'call'}">
                  <awicon slot="title-icon" name="comments" :scale="100"></awicon>
                  <span slot="title">{{profile.agency.contact}}</span>
                  <span slot="subtitle">{{captions.forms.labels.or}}</span>
                </AjaxForm>
              </div>

              <div id="pf-email-form" class="pf-popup-form">
                <AjaxForm :form="forms.email" :view="1" :data="{username:profile.info.username, form_type:'email'}">
                  <awicon slot="title-icon" name="comments" :scale="100"></awicon>
                  <span slot="title">{{captions.forms.labels.send_mail}}</span>
                </AjaxForm>
              </div>

            </div>
          <!-- END FORMS -->
          <h2>
            {{profile.vitals.parent1.first_name}}
            <template v-if="profile.vitals.parent2">
              & {{profile.vitals.parent2.first_name}}
            </template>
          </h2>
          <div v-if="profile.info.short_description">
            {{profile.info.short_description}}

            <span v-if="captions.profile.letters" class="pf-profile-g-footer">
             <a href="#" @click.prevent="$refs.lettersTab.open = true"
                          v-scroll-to="{
                            el: '#letters-'+profile.info.username
                          }" >

                          {{captions.advMore}}
              </a>
            </span>
          </div>

        </div>
      </div>
      <!-- END INFO -->



      <div class="pf-cf">


       <!-- GALLERIES -->
        <div class="pf-profile-galleries">

          <div class="pf-profile-gallery">
            <div ref="profile_videos_container" v-if="profile.photos && profile.photos.length">
              <isotope ref="profile_videos"  :options='getOptions()'  v-images-loaded:on.progress="layout" :list="profile.photos">
                <div class="pf-profile-g-item" v-for="(item, indx) in profile.photos.slice(0,photoNumber)" :key="indx" :data-intro="(indx==0)?captions.tooltips.photos:null">
                  <a :href="item.full" :data-group="'gallery-'+profile.info.username" @click.prevent ="openFancybox" ><img :src="item.thumb" alt=""></a>
                </div>
              </isotope>

              <div style="display:none;">
                <a :href="item.full" :data-group="'gallery-'+profile.info.username" @click.prevent ="openFancybox" v-for="(item, indx) in profile.photos.slice(photoNumber)" :key="indx"></a>
              </div>

            </div>

            <div class="pf-profile-g-footer" v-if="profile.photos.length>photoNumber">
              <a href="#" @click.prevent = "openPhotoGallery" >{{captions.profile.more_photos}}</a>
            </div>
          </div>

          <!-- PROFILE VIDEO -->

          <div class="pf-profile-video-wrap">

            <template v-if="profileVideo">
              <div class="pf-profile-video pf-video-youtube" v-if="profileVideo.type=='youtube'" :data-intro="captions.tooltips.video">
                <a :href="profileVideo.url" @click.prevent ="openFancybox">
                  <span class="pf-play-icon">
                    <span></span>
                    <div>{{captions.profile.watch}}</div>
                  </span>
                  <img :src="profileVideo.thumb" alt="">
                </a>
              </div>

              <div class="pf-profile-video" v-if="profileVideo.type=='video'" :data-intro="captions.tooltips.video">
                <PopupVideo :id="'pf-profile-video-'+profile.info.username" :url="profileVideo.url"></PopupVideo>

              </div>



            </template>


            <div class="pf-profile-g-footer" v-if="videos && videos.length">
              <a href="#" @click.prevent="$refs.videoTab.open = true"
                          v-scroll-to="{
                            el: '#videos-'+profile.info.username,
                            onDone: onVideoScrollDone
                          }"
                          v-if="videos.length">

                          {{captions.profile.more_videos}}
              </a>
            </div>
          </div>
          <!-- END PROFILE VIDEO -->
        </div>


       <!-- END GALLERIES -->
      </div>

      <!-- TABS -->

        <Tab :title="captions.profile.vitals" v-if="profile.vitals && (!childpreferencesEmpty || !parentsVitalsEmpty)" :data-intro="captions.tooltips.content">
          <awicon name="bar-chart" slot="icon"></awicon>

          <div class="pf-vitals pf-cf">

            <div class="pf-vit-table-wrap pf-vit-adoption">
              <table class="pf-vit-table">
                <thead>
                  <tr>
                    <th class="pf-vit-title">{{captions.profile.preferences}}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="pf-vit-row" v-if="profile.vitals.childpreferences.ethnicity && profile.vitals.childpreferences.ethnicity.length">
                    <td>
                      <div><strong>{{captions.profile.ethnicity}}:</strong></div>
                      <div><span v-html="capitalizeWords(profile.vitals.childpreferences.ethnicity)"></span></div>
                    </td>
                  </tr>
                  <!--  -->

                  <tr class="pf-vit-row" v-if="profile.vitals.childpreferences.ageGroup && profile.vitals.childpreferences.ageGroup.length">
                    <td>
                      <div><strong>{{captions.profile.age}}:</strong></div>
                      <div><span v-html="profile.vitals.childpreferences.ageGroup.split(',').join(', ')"></span></div>
                    </td>
                  </tr>
                  <!--  -->

                  <tr class="pf-vit-row" v-if="profile.vitals.childpreferences.adoption && profile.vitals.childpreferences.adoption.length">
                    <td>
                      <div><strong>{{captions.profile.adoption_type}}:</strong></div>
                      <div><span v-html="profile.vitals.childpreferences.adoption.split(',').join(', ')"></span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="childpreferencesEmpty">
                    <td colspan="2">
                      <center><span>{{captions.profile.nothing_to_show}}</span></center>
                    </td>
                  </tr>
                  <!--  -->
                </tbody>
              </table>
            </div>

            <div :class="['pf-vit-table-wrap',  'pf-vit-parents', {'pf-vit-with-footer': profile.vitals.waiting && profile.vitals.waiting!='0'}]">
              <table :class="['pf-vit-table', 'pf-style-2', 'pf-vit-table-'+vitalsParents.length]">
                <thead>
                  <tr>
                    <th class="pf-vit-title" v-for="parent in vitalsParents">
                      {{parent.first_name}}
                    </th>
                  </tr>
                </thead>
                <tbody>


                  <tr class="pf-vit-row" v-if="profile.displayOptions.show_family_ethnicity == 'show'">
                    <td v-for="parent in vitalsParents" v-if="parent.ethnicity">
                      <div><strong>{{captions.profile.ethnicity}}:</strong></div>
                      <div><span>{{parent.ethnicity}}</span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="profile.displayOptions.show_education == 'show'">
                    <td v-for="parent in vitalsParents" v-if="parent.education">
                      <div><strong>{{captions.profile.education}}:</strong></div>
                      <div><span>{{parent.education}}</span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="profile.displayOptions.show_gender == 'show'">
                    <td v-for="parent in vitalsParents" v-if="parent.gender">
                      <div><strong>{{captions.profile.gender}}:</strong></div>
                      <div><span v-html="capitalizeWords(parent.gender)"></span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="profile.displayOptions.show_religion == 'show'">
                    <td v-for="parent in vitalsParents" v-if="parent.religion">
                      <div><strong>{{captions.profile.religion}}:</strong></div>
                      <div><span v-html="capitalizeWords(parent.religion)"></span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="profile.displayOptions.show_family_age == 'show'">
                    <td v-for="parent in vitalsParents" v-if="parent.dob">
                      <div><strong>{{captions.profile.age}}:</strong></div>
                      <div><span v-html="calculateAge(parent.dob)"></span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="profile.vitals.state">
                    <td colspan="2">
                      <div><strong>{{profile.vitals.stateLabel}}:</strong></div>
                      <div><span v-html="capitalizeWords(profile.vitals.state)"></span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="profile.vitals.no_of_children">
                    <td colspan="2">
                      <div><strong>{{captions.profile.no_of_children}}:</strong></div>
                      <div><span v-html="capitalizeWords(profile.vitals.no_of_children)"></span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="profile.vitals.childrensAge">
                    <td colspan="2">
                      <div><strong>{{captions.profile.childrens_age}}:</strong></div>
                      <div><span v-html="capitalizeWords(profile.vitals.childrensAge)"></span></div>
                    </td>
                  </tr>
                  <!--  -->
                  <tr class="pf-vit-row" v-if="parentsVitalsEmpty">
                    <td colspan="2">
                      <center><span>{{captions.profile.nothing_to_show}}</span></center>
                    </td>
                  </tr>
                  <!--  -->
                </tbody>
              </table>

              <div class="pf-vit-table-footer" v-if="profile.vitals.waiting && profile.vitals.waiting!='0'">
                <awicon name="clock-o"></awicon>
                {{captions.profile.waiting}} {{profile.vitals.waiting}}
              </div>
            </div>

          </div>


        </Tab>


        <Tab :title="captions.profile.journals" v-if="profile.journals && profile.journals.length" >
          <awicon name="pencil-square-o" slot="icon"></awicon>

          <Journals :list="profile.journals"></Journals>
        </Tab>

        <Tab :id="'letters-'+profile.info.username" :title="captions.profile.letters"  ref="lettersTab" v-if="profile.letters && profile.letters.length" >
          <awicon name="clipboard" slot="icon"></awicon>

          <Journals :list="profile.letters"></Journals>
        </Tab>

        <Tab :title="captions.profile.about_us" v-if="profile.about_us">
          <awicon name="user" slot="icon" class="pf-icon-user"></awicon>


        </Tab>

        <Tab :id="'videos-'+profile.info.username" ref="videoTab" :title="captions.profile.videos" v-if="videos.length" >
          <awicon name="user" slot="icon" class="pf-icon-user"></awicon>
          <div class="pf-video-list">
            <div class="pf-v-item" v-for="(video, index) in videos" :key="index">
               <a class="pf-v-image" @click.prevent ="openFancybox" :href="video.url">
                  <img :src="YouTubeGetThumb(video.id)" alt="">
                  <span class="pf-play-icon"><span></span></span>
                </a>

                <h5><a @click.prevent ="openFancybox" :href="video.url" v-html="decodeURIComponent(video.label)"></a></h5>
            </div>
          </div>


        </Tab>
      <!-- END TABS -->
    </div>

 </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import _reduce from 'lodash/reduce';
import _debounce from 'lodash/debounce';
import _throttle from 'lodash/throttle';
import isotope from './../../isotope.js'
import imagesLoaded from 'vue-images-loaded'
import axios from 'axios'
import {capitalize} from '../../store/utils'
import Cookie from 'tiny-cookie';
import $ from 'jquery';

import 'vue-awesome/icons/bar-chart';
import 'vue-awesome/icons/pencil-square-o';
import 'vue-awesome/icons/clipboard';
import 'vue-awesome/icons/file-text-o';
import 'vue-awesome/icons/phone';
import 'vue-awesome/icons/comments';
import 'vue-awesome/icons/envelope';
import 'vue-awesome/icons/file-pdf-o';
import 'vue-awesome/icons/clock-o';
import 'vue-awesome/icons/user';
import 'vue-awesome/icons/link';

import awicon from 'vue-awesome/components/Icon.vue';

import Tab from './Tab.vue';
import Journals from './Journals.vue';
import PopupVideo from './video.vue';
import AjaxForm from './Form.vue';
import Icon from 'vue-svg-icon/Icon.vue';



export default {
  props:['username', 'popup'],
  computed: {
    forms: function() {
      return {
        call: [
          {
            name: 'callback',
            placeholder: this.captions.forms.labels.call_me_back,
            type: 'checkbox',
            disableHandler: true
          },
          {
            name: 'time',
            placeholder: this.captions.forms.labels.select,
            options: this.captions.forms.labels.time,
            type: 'select',
            required: true
          },
          {
            name: 'phone_number',
            placeholder: this.captions.forms.labels.number,
            type: 'text',
            required: true
          }
        ],
        chat: [
          {
            name: 'phone_number',
            placeholder: this.captions.forms.labels.number,
            type: 'text',
            required: true
          },

          {
            name: 'message',
            placeholder: this.captions.forms.labels.message,
            type: 'textarea',
            required: true
          }
        ],
        email: [
          {
            name: 'your_email',
            placeholder: this.captions.forms.labels.email,
            type: 'email',
            required: true
          },

          {
            name: 'message',
            placeholder: this.captions.forms.labels.message,
            type: 'textarea',
            required: true
          }
        ]


      }
    },
    tooltipButtons: function() {
      var list = [],
          captions = this.captions.profile;

      let phone = this.profile.agency.contact;

      if(phone && phone!=='0'){
        list.push({
          label: captions.phone,
          icon: 'phone',
          link: '#pf-call-form',
          type: 'inline',
          fancybox: true
        });
      }

      list.push({
        label: captions.email,
        icon: 'envelope',
        link: '#pf-email-form',
        type: 'inline',
        fancybox: true,
      });


      list.push({
        label: captions.chat,
        icon: 'comments',
        link: '#pf-chat-form',
        type: 'inline',
        fancybox: true
      });


      var pdf = this.profile.info.download;
      var flipbook = this.profile.info.flipbook;
      var website = this.profile.info.website;

      if(pdf && pdf!='0') {
        list.push({
          label: captions.download,
          icon: 'file-pdf-o',
          link: pdf,
          target: "_blank"
        });
      };

      if(flipbook && flipbook!='0') {
        list.push({
          label: captions.flipbook,
          svg: true,
          icon: 'flipbook',
          link: flipbook,
          target: "_blank"
        });
      };

      if(website && website!='0') {
        list.push({
          label: captions.website,
          svg: true,
          icon: 'website',
          link: website,
          target: "_blank"
        });
      };

      var emptyCells = (list.length%3!==0)?(3-list.length%3):0;

      for (var i = 0; i < emptyCells; i++) {
        list.push({});
      }
      return list;
    },
    profileVideo: function() {

      var _this = this,
          video = _this.profile.info.video;

      if(!video) return null;

      var videoID =  _this.YouTubeGetID(video);

      var thumb = '',
          type = '';

      if (videoID == -1) {
        // if(video.search('.mp4')==-1) return null;
        type = 'video';
      } else {
        type = 'youtube';
        thumb = _this.YouTubeGetThumb(videoID);
      };


      return {
        type: type,
        url: video,
        id: videoID,
        thumb: thumb
      }
    },

    videos: function() {
      var _this = this;
      var videos = _reduce(_this.profile.videos, function(acc, v){
        var id = _this.YouTubeGetID(v.video);
        if(id !== -1) {
          acc.push({
            label: v.label,
            url: v.video,
            id: id
          });
        }

        return acc;
      } , []);
      return videos;
    },

    vitalsParents: function() {
      var parents = [];
      if(this.profile.vitals.parent1) {
        parents.push(this.profile.vitals.parent1);
      }

      if(this.profile.vitals.parent2) {
        parents.push(this.profile.vitals.parent2);
      }

      return parents;
    },
    childpreferencesEmpty: function() {
      return (!this.profile.vitals.childpreferences.ethnicity && !this.profile.vitals.childpreferences.adoption && !this.profile.vitals.childpreferences.ageGroup);
    },
    parentsVitalsEmpty: function() {
      return ((!this.profile.vitals.parent1.ethnicity && (this.profile.vitals.parent2 && !this.profile.vitals.parent2.ethnicity)) && (!this.profile.vitals.parent1.education && (this.profile.vitals.parent2 && !this.profile.vitals.parent2.education)) && (!this.profile.vitals.parent1.gender && (this.profile.vitals.parent2 && !this.profile.vitals.parent2.gender)) && (!this.profile.vitals.parent1.religion && (this.profile.vitals.parent2 && !this.profile.vitals.parent2.religion)) && (!this.profile.vitals.parent1.age && (this.profile.vitals.parent2 && !this.profile.vitals.parent2.age)) && !this.profile.vitals.state && !this.profile.vitals.no_of_children && !this.profile.vitals.waiting);
    },

    ...mapGetters(['getProfile', 'captions', 'initialized', 'loading', 'favorites', 'options'])
  },
  data: function () {
    return {
      profile: null,
      error: null,
      user: null,
      photoNumber: 8,
      isFavorite: false,
      videoThumbs: {}
    }
  },

  beforeMount () {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData()
  },
  watch: {

    initialized: 'fetchData',
    '$route': 'fetchData'
  },
  directives: {
    imagesLoaded
  },
  components: {Tab, awicon, Journals, isotope, imagesLoaded, Icon, PopupVideo, AjaxForm},
  created() {
    this.scrollToTop('.pf_b-app-wrapper');
  },
  methods: {
    openFancybox: (e) => {
      var $el = $(e.currentTarget),
          $links = $el;

      e.preventDefault();

      var opt = Object.assign({},window.Vue.FANCYBOX);



      if($el.data('type')) {
        opt.type = $el.data('type');
      }


      if($el.data('group') && $el.data('group').length) {
          $links = $('[data-group="'+$el.data('group')+'"]')
      }

      $.fancybox.open( $links, opt, $links.index( $el ) );

      return false;
    },
    capitalizeFirstLetter: (str) => {
      return capitalize(str);
    },
    capitalizeWords: (str) => {
      return str.split(' ').map(w => capitalize(w)).join(' ');
    },
    calculateAge: (dob) => {
      var birthday = new Date(dob);
      var ageDifMs = Date.now() - birthday.getTime();
      var ageDate = new Date(ageDifMs);
      return Math.abs(ageDate.getUTCFullYear() - 1970);
    },
    toggleFavorite() {
      this.isFavorite = !this.isFavorite;
      this.$store.commit('updateFavorites', {
        toAdd: this.isFavorite,
        username: this.profile.info.username
      })
    },
    onVideoScrollDone: function() {

    },
    openPhotoGallery: function() {

      this.$refs.profile_videos_container.querySelectorAll('a')[this.photoNumber].click();
    },
    onProfileLoaded: _throttle(function (e, img) {
      var _this = this;

      this.$nextTick(function () {
        this.$store.commit('setLoading', false);
          var COOKIE_INTRO_NAME = 'pf-intro',
              cookie = Cookie.get(COOKIE_INTRO_NAME);

          if (cookie === null) {
             introJs().setOptions({
                tooltipClass: 'pf-intro-tooltip',
                nextLabel:_this.captions.tooltips_buttons.next,
                prevLabel:_this.captions.tooltips_buttons.prev,
                skipLabel:_this.captions.tooltips_buttons.skip,
                doneLabel:_this.captions.tooltips_buttons.done

              }).start();

            var now = new Date;
            now.setMonth(now.getMonth() + 1);

            Cookie.set(COOKIE_INTRO_NAME, '1', { expires: now.toGMTString() });
          };


          if(this.popup) {
            var opt = Object.assign({},window.Vue.FANCYBOX);

            $.fancybox.open({
              src  : this.popup,
              type : 'inline',
              opts : opt

            });
          }


      })
    }, 500),
    // YouTubeGetID: function(url){
    //   var ID = '';
    //   url = url.replace(/(>|<)/gi,'').split(/(vi\/|v=|\/v\/|youtu\.be\/|\/embed\/)/);
    //   if(url[2] !== undefined) {
    //     ID = url[2].split(/[^0-9a-z_\-]/i);
    //     ID = ID[0];
    //   }
    //   else {
    //     ID = url;
    //   }
    //     return ID;
    // },
    layout () {
      let _this = this;



      this.$refs.profile_videos.layout('masonry');
      Object.assign(this.$refs.profile_videos.iso.options, {transitionDuration: '0.3s'})
    },
    getOptions: function () {
      var _this = this;
      return {
        layoutMode: 'masonry',
        transitionDuration: 0,
        percentPosition: true,
        itemSelector: '.pf-profile-g-item'
      }
    },
    YouTubeGetID: function(url){

      if (url != undefined || url != '') {
          var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=|\?v=)([^#\&\?]*).*/;
          var match = url.match(regExp);
          if (match && match[2].length == 11) {
            return match[2];
          }
          else {
            return -1;
          }
      }
    },
    YouTubeGetThumb: function(id) {
      return 'http://img.youtube.com/vi/'+id+'/0.jpg';
    },
    fetchData () {
      var _this = this;

      if(_this.initialized) {

        _this.profile = null;
        // _this.loading = true;


        axios.get(window.Vue.HTTP_PATHS.profile, {
          params: {
            request: "profile",
            username: _this.username

          }
        }).then(response => {
          this.$store.commit('setLoading', false);
          _this.profile = response.data;
          const info = _this.profile.info;
          if(!info || info.account_id === 0) {
            this.error = this.captions.not_found;
          } else if(!info.is_enable) {
            this.error = this.captions.disableProfile;
          } else {
            _this.error = null;
            _this.isFavorite = _this.favorites(_this.profile.info.username);
            let id = _this.profile.info.account_id;

            _this.user =  _this.getProfile(id);
          }


        })
        .catch(e => {
          console.error(e);
        })
      }


    },
    scrollToTop: function(elem) {     
      var container = $(elem);
      $('html, body').animate({
          scrollTop: container.offset().top
      }, 2000);
    },
  }
}
</script>

